import React from "react";
import { useState } from "react";

const CreateTask = (props) => {
  const [title, setTitle] = useState("");

const onTitle = (ev) => {
    setTitle(ev.target.value)
}

const onCreateTask = (ev) => {
    ev.preventDefault();
    if(title.length > 0) {
        props.onTaskTitle(title);
        setTitle("")
    }
}

  return (
    <React.Fragment >
      <form onSubmit={onCreateTask}>
        <input
          class="flex flex-1 shadow-sm focus:ring-indigo-500 focus:border-indigo-500 p-1.5 block w-full sm:text-sm border-gray-300 rounded-md"
          placeholder="Set the task title"
          value={title}
          onChange={onTitle}
        ></input>
      </form>
    </React.Fragment>
  );
};

export default CreateTask;
